.contact-light,.contact-dark{
    /* background-color: red;    */
    width: 85%;
    margin: auto;
    margin-left: 15%;
    padding: 1%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}

.link-div-light,.link-div-dark{
    margin: 1%;
    width: 48%;
    height: 8vh;
    display: flex;
    align-items: center;
}

.link-div-dark a,.link-div-light a{
    color: #f5f5f5;
    position: relative;
    /* background-color: red; */
}

.link-div-light a{
    color: #333;
}

.link-div-dark a::after,.link-div-light a::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    bottom: 0.5px;
    left: 11px;
    left: 0px;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.3s ease; 
}


.link-div-dark a:hover::after,.link-div-light a:hover::after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

.link-div-light a::after{
    background-color: #333;
}

.link-div-dark img{
    background-color: #f5f5f5;
    border-radius: 6px;
    padding: 2%;
}

.image-link{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    aspect-ratio: 1;
    border-radius: 5px;
    /* border:1px solid white; */
    /* background-color: blue;   */
}

.image-link img{
    border-radius: 5px;
    height: 80%;
}

.link-data{
    text-decoration: none;
    font-family: raleway;
    font-size: 1.2rem;
    margin: 0 1%;
    padding: 0.5% 0;
    transition: all ease-in-out 0.3s;
    /* padding: 1% 2%; */
}