.projects-section{
    min-height: 60vh;
    width: 100%;
    padding: 1%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

.project-box-light,.project-box-dark{
    width: 40%;
    min-height: 70vh;
    background-color: #fff;
    /* border: 2px solid saddlebrown; */
    box-shadow: 0px 0px 1px 1px #eee;
    display: flex;
    flex-direction: column;
    padding: 1%;
    transition: all ease-in-out 0.3s;
}

.project-box-dark{
    color: #eee;
    background-color: #2d2d2d;
    box-shadow: 0px 0px 1px 1px #222;
}
.project-box-dark div{
    color: #eee;
}

.image-box{
    width: 100%;
    overflow: hidden;
    border: 1px solid #888;
    transition: all ease 0.2s;
    &:hover{
        margin-top: -1%;
        transform: scale(1.057);
        filter: brightness(1.04);
        /* box-shadow: 0 0 2px 1px #888; */
        margin-bottom: 1.5%;
    }
}

.image-box>img{
    width: 100%;
}

.details-box{
    /* background-color: red; */
    padding: 1%;
}

.project-name-light,.project-name-dark{
    font-size: 2.1rem;
    font-weight: 600;
    padding: 1%;
    text-align: center;
    transition: all ease-in-out 0.3s;
}

.project-name-light>a,.project-name-dark>a{
    text-decoration: none;
    position: relative;
}

.project-name-dark>a{
    color: #f5f5f5;
}

.project-name-light>a::after,.project-name-dark>a::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #444;
    bottom: 0.5px;
    left: 0;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.3s ease; 
}

.project-name-dark>a::after{
    background-color: #eee !important;
}

.project-name-light>a:hover::after,.project-name-dark>a:hover::after{
    transform: scaleX(1);
    transform-origin: bottom left;
}

.project-details{
    text-align: left;
    font-size: 1.2rem;
    font-weight: 400;
    white-space: pre-wrap;
}

.link{
    margin: 0 2%;
    height: 1.4rem;
}