.certifications{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    /* background-color: red; */
}

.certificate-frame{
    background-size: contain;
    width: 40vw;
    min-height: 29vw;
    margin: 2%;
    /* margin-bottom: 8%; */
    transform: scale(0.97);
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
}

.certificate{
    height: 27.85vw;
    width: 40vw;
    transform: scale(0.95);
}

.certificate img{
    width: 40vw;
    height: 29vw;
    transform: scale(1.00);
    margin-top: -1.45%;
    margin-left: 0.15%;
    /* box-shadow: 3px 3px 5px 2px #888; */
    filter: contrast(1.2) grayscale(0);
}

#c3{
    background: url('../../public/Assets/CN_BackEnd_CertificateOfCompletion_page-0001.jpg');
    background-size: contain;
}
#c1{
    background: url('../../public/Assets/CN_BackEnd_CertificateOfExcellence_page-0001.jpg');
    background-size: contain;
}
#c2{
    background: url('../../public/Assets/CN_FrontEnd_CertificateOfExcellence_page-0001.jpg');
    background-size: contain;
}
#c4{
    background: url('../../public/Assets/CN_FrontEnd_CertificateOfCompletion_page-0001.jpg');
    background-size: contain;
}
#c5{
    background: url('../../public/Assets/CN_AdvancedFrontEnd_CertificateOfCompletion-1.png');
    background-size: contain;
}
#c6{
    background: url('../../public/Assets/Certificate_page-0001.jpg');
    background-size: cover;
}

#c6 img{
    margin-top: -0.9%;
}

.certificate-name-light,.certificate-name-dark{
    margin-top: 2%;
    text-align: center;
    font-weight: 500;
    font-size: 2.2rem;
    color: #333;
    transition:  all ease-in-out 0.3s;
}

.certificate-name-dark{
    color: #fff;
}

.certificate-name2-light,.certificate-name2-dark{
    text-align: center;
    font-weight: 400;
    font-size: 1.2rem;
    color: #444;
    transition:  all ease-in-out 0.3s;
}

.certificate-name2-dark{
    color: #eee;
}