.section-heading-dark,.section-heading-light{
    font-family: raleway;
    font-size: 3.2rem;
    font-weight: 600;
    margin: 3% 3% 1% 3%;
    text-align: center;
    color: #222;
    position: relative;
    transition: all ease-in-out 0.3s;
}

.section-heading-dark{
    color: #f5f5f5;
}

.main-section {
    min-height: 50vh;
    width: 100%;
    padding: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.loading-spinner{
    position: absolute;
    width: 0px;
    height: 0px;
    top: 48vh;
    left: 49vw;
}