.education{
    /* background-color: red; */
    padding: 1%;
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.education-light-theme,.education-dark-theme {
    min-height: 50vh;
    width: 100%;
    padding: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    transition: all ease-in-out 0.3s;
}

.education-light-theme div{
    transition: all ease-in-out 0.3s;
}

.education-dark-theme div{
    color: #eee !important; 
}

.institute{
    width: 87%;
    /* background-color: rebeccapurple; */
    display: flex;
    flex-direction: column;
    height: 15vh;
    justify-content: space-evenly;
}

.sub-institute1{
    font-size: 1.8rem;
    font-weight: 500;
}

.sub-institute2{
    font-size: 1.3rem;
    padding-left: 2%;
}

.grade{
    font-family: poppins;
    font-size: 1.4rem;
    font-weight: 300;
}