header{
    padding: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 10vh;
    /* background-color: blue; */
}


.theme{
    position: absolute;
    right: 2vw;
    top: 1.6vh;
    background-color: #f5f5f5;
    box-shadow: 0 0 3px 1px #777;
    width: 3.4vw;
    height: 3.4vw;
    padding: 1%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;
}

.light{
    background-color: #444;
    /* box-shadow: 0 0 3px 1px #999; */
}

.theme>img{
    height: 2.8vw;
    aspect-ratio: 1;
}

.navbar-list-light-theme,.navbar-list-dark-theme{
    z-index: 1;
    height: 6vh;
    padding: 0;
    border-radius: 5vw;
    width: 65%;
    list-style: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.navbar-itemlight-theme,.navbar-itemdark-theme{
    border-radius: 5vw;
    padding: 0.8% 2%;
    width: max-content;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.navbar-link{
    border-radius: 5vw;
    position: relative;
    font-size: 1.2rem;
    color: #444;
    font-weight: 400;
    font-family: 'star jedi';
    font-family: raleway,poppins;
    text-decoration: none;
    transition: all ease-in-out 0.3s;
    &:hover{
        color: #000;
    }
}

.navbar-link::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #444;
    bottom: 0.5px;
    left: 0;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.3s ease; 
}

.navbar-link:hover::after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

.navbar-link.selected::before {
    content: "";
    background-color: #333;
    width: 100%;
    position: absolute;
    bottom: 0.5px;
    left: 0px;
    height: 2px;
}

.navbar-list-dark-theme>li>a{
    color: #f5f5f5;
    &:hover{
        color: #fff;
    }
}

.navbar-itemdark-theme>a::after{
    background-color: #f5f5f5;
}